.not-found {
	overflow: hidden;
	height: 100vh;
}

.notfound-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.notfound-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
	z-index: 999;
}

.notfound-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px;
}

.notfound-message {
	position: fixed;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.notfound-title {
	font-family: var(--secondary-font);
	color: var(--primary-color);
	font-size: 80px;
	padding-top: 15px;
	font-weight: 700;
}

.not-found-message {
	color: var(--secondary-color);
	font-size: 2rem;
	margin: 2rem 0;
	text-align: center;
}

.not-found-link {
	font-size: 1.5rem;
	text-decoration: none;
	color: #fff;
	background-color: var(--link-color);
	padding: 1rem 2rem;
	border-radius: 50px;
	transition: all 0.3s ease-in-out;
}

.not-found-link:hover {
	background-color: #fff;
	color: var(--link-color);
	box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
}

@media (max-width: 500px) {
	.notfound-message {
		position: absolute;
		top: auto;
		left: auto;
		width: 100%;
		transform: none;
	}

	.notfound-title {
		font-size: 50px;
	}

	.not-found-message {
		width: 100%;
		font-size: 15px;
		margin-top: 10px;
		margin-left: -5px;
		margin-right: 32px;
	}

	.not-found-link {
		font-size: 15px;
		padding: 15px 15px;
	}
}

::-webkit-scrollbar {
	width: 10px;
}

::-webkit-scrollbar-track {
	background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
	background-color: #383636;
	border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
	background-color: rgb(0, 0, 0);
}

.page-content {
	background-color: white;
	position: relative;
}

.page-content:before,
.page-content:after {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	width: calc((100% - 1200px) / 2);
	background-color: #fafafa;
	border: 1px solid #f4f4f5;
}

.page-content:before {
	left: 0;
	z-index: 1;
}

.page-content:after {
	right: 0;
	z-index: 1;
}

.content-wrapper {
	max-width: 1000px;
	margin: 0 auto;
}

.title {
	color: var(--primary-color);
	font-family: var(--secondary-font);
	font-size: 45px;
	font-weight: 700;
	width: 70%;
}

.subtitle {
	padding-top: 10px;
	color: var(--secondary-color);
	font-size: 16px;
	line-height: 28px;
	width: 70%;
	margin-top: 25px;
}

.page-footer {
	position: block;
}

.page-footer::before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	border-top: 2px solid var(--tertiary-color);
}

@media (max-width: 1270px) {
	.page-content:before,
	.page-content:after {
		width: calc((100% - 90%) / 2);
	}

	.content-wrapper {
		max-width: 90%;
		padding-left: 10%;
		padding-right: 10%;
	}
}

@media (max-width: 1024px) {
	.page-content:before,
	.page-content:after {
		width: calc((100% - 950px) / 2);
	}

	.content-wrapper {
		max-width: 980px;
	}
}

@media (max-width: 800px) {
	.content-wrapper {
		max-width: 90%;
		margin: 0 auto;
		padding: 0 10px;
	}

	.page-content:before,
	.page-content:after {
		display: none;
	}

	.title {
		width: 100%;
	}

	.subtitle {
		width: 100%;
	}
}

